import shelljs from 'shelljs'

export function shell(options = {}){
  options.async = true

  let proc = shelljs(options.cmd, options)

  let { data, log } = options

  proc.stdout.on('data', function(buffer){
    data(
      buffer.toString()
    )
  })

  return proc
}



